.cu {
    font-family: 'Redaction', serif, system-ui;
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 56px;
    padding-top: 180px;
}

.ld {
    font-family: 'Redaction', serif, system-ui;
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 107px;
    margin-top: -30px;
}

btn-primary {
    background-color: #ffffff;
    border-color: #ffffff;
    color: #fff;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    cursor: pointer;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    margin-top: 20px;
}

.btn-primary:hover {
    background-color: #ffffff;
    border-color: #ffffff;
    color: #000;
}

.btn-primary:focus {
    background-color: #ffffff;
    border-color: #ffffff;
    color: #000;
}

.logo {
    position: absolute;
    // top: 3rem;
    // left: 7rem;
    width: 10rem; 
}

.logo2 {
    align-self: flex-end; 
}

.logo3 {
    align-self: flex-start;
}

.hero-text {
    background-color: rgba(64, 224, 208, 0.482);
    position: absolute;
    top: 10rem;
    left: 8rem;
    width: 60%; 
    // max-width: 50%; 
}   

.top-nav {
    width: 80%; 
    height: 10rem;
    background-color: rgba(64, 224, 208, 0.482);
    position: absolute;
    left: 8rem;
    top: 3rem;
    z-index: auto;
    display: flex;
    // justify-content: space-evenly;

}

.icon {
    width: 3rem; 
    height: 3rem;
}

.side-bar {
    border-left: 1px solid white;
    width: 8rem; 
    height: 100%;
    right: 0;
    position: absolute; 
}

.sidebar {
    width: 8rem; 
    height: 100%;
    // background-color: rgba(200, 224, 64, 0.183);
    position: fixed; 
    right: 0; 
    // border-left: 1px solid white;
    // mix-blend-mode: difference; 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

li {
    float:left ;
}

.up {
    font-size: 40px;
    position: relative;
    right: -10rem; 
    font-family: 'figtree-medium', sans-serif, system-ui;
}

